/* index.css */
body {
  font-family: Arial, sans-serif;
}
.card-container {
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;
  justify-content: center;
}
.topnav {
  display: flex;
  width: 98%;
  
  position: fixed;
  border-radius: 30px;
  justify-content: space-between; /* Space items evenly */
  align-items: center; /* Center items vertically */
  background-color: #333;
  padding: 3px;
}

.blocks {
  padding-top: 75px;
  font-size: 20px;
  max-width: 480px;
  margin: 0 auto;
  text-align: center;
}

.nav-right {
  display: flex;
  align-items: center; /* Center items vertically */
  margin-left: auto; /* Push items to the right */
}

.topnav a{
  float: left;
  color: #f2f2f2;
  text-decoration: none;
  padding: 0px 15px;
  font-size: 17px;
}
.topnav b {
  font-family: "Roboto", sans-serif;

  float: left;
  color: #f2f2f2;
  text-align:center;
  padding: 10px 10px;
  text-decoration: none;
  font-size: 30px;
}
.topnav .active {
  font-weight: bold;
}

.main {
  padding: 20px;
}
